<template>
	<div class="home container pt-h">
		<Homewel @float-act="floatAct" :float="float" @blur-act="blurAct" :blurring="blurring"/>
		<Homeflo :float="float" :blurring="blurring"/>
	</div>
</template>

<script>
	import Homewel from './Homewel.vue';
	import Homeflo from './Homeflo.vue';
	export default {
		name: 'home',
		components: {
			Homewel,
			Homeflo
		},
		data(){
			return{
			float: false,
			blurring: false
			}
		},
		methods:{
			floatAct: function(){
				this.float = true;
			},
			blurAct: function(){
				this.blurring = !this.blurring;
			}
		}
		
	}
</script>
