<template>
	<div id="floaties" class="blurfx" :class="{'no-blurfx':blurring}">
		<HomefloItem  class="textf" v-for="word in wordList" :key="word" :text="word" :float="float" />
	</div>
</template>

<script>
	import HomefloItem from './Homeflo-item.vue';
	export default{
		name: 'homeflo',
		components:{
			HomefloItem
		},
		props:{
			float: Boolean,
			blurring: Boolean
		},
		data(){
			return{
				wordList: ["WEB DEV", "PROGRAMMING", "JAVASCRIPT", "HTML","HTML, CSS","Javascript","JQuery","SASS","Vue.js","Gulp","Json",],
			}
		},
	}
</script>