<template>
	<div class="welcomebox" v-on:mousemove="$emit('float-act')" v-on:mouseover="$emit('blur-act')" v-on:mouseout="$emit('blur-act')">
		{{textload}}
		<div class="inst" :class="{'d-none':float}">(Hover/Tap on me)</div>
	</div>
</template>

<script>
	export default {
		name: 'homewel',
		props:{
			float: Boolean
		},
		data(){
			return{
				displayWords: ["Hello!", "I am Rochale Anne", "A Creative Web Developer"],
				textload: '',
				numberNext:0,
			}
		},
		created(){
			this.entertext(this.numberNext);
		},
		methods: {
			entertext: function(numberNext){
				var countLetter = this.displayWords[numberNext].split("").length+1;
				var count = 0;
				var ref = this;
				var letterPlay = setInterval(function(){
					count++
					if (count == countLetter){
						clearInterval(letterPlay);
						setTimeout(function(){
							ref.clearText(count, numberNext);
						},200);
					} 
					else {
						ref.keyIn(count,numberNext);	
					}
				},100);
			},
			keyIn: function(num,arrNum){
				var text = this.displayWords[arrNum].split("");
				this.textload += text[num-1];
			},
			clearText: function(num,numberNext){
				var maxArr = this.displayWords.length-1;
				var ref = this;
				var timeOut = setInterval(function(){	
					if (num == 0){
						clearInterval(timeOut);
						numberNext++
						if(numberNext > maxArr){
							numberNext = 0;
							ref.entertext(numberNext);
						} else {
							ref.entertext(numberNext);
						}
					} else {
						num--;			
						ref.keyOut();
					}
				},100);
			},
			keyOut: function(){
				var currentStr = this.textload;
				this.textload = currentStr.slice(0,-1);
			}
		}
	}
</script>
