<template>	
	<div :style="'left:'+numleft+'%;top:'+numtop+'%;opacity:'+opacity">{{text}}</div>
</template>

<script>
	export default{
		name: 'homeflo-item',
		props:{
			text: String,
			float: Boolean
		},
		data(){
			return{
				numleft: 50,
				numtop: 50,
				opacity: 0,
			}
		},
		watch: {
			float: function(){
				this.pushNum();
			}
		},
		methods:{
			rend: function(){
				return Math.ceil(Math.random()*100);				
			},
			pushNum: function(){
				var ref = this;
				setInterval(function(){
					ref.numleft = ref.rend();
					ref.numtop = ref.rend();
					ref.opacity = 1;
				}, 1000);
			},
			
		}
	}
</script>